import { registerBlockType } from "@wordpress/blocks";
import "./style.scss";
import Edit from "./edit";

registerBlockType("create-block/veramine-banner", {
	title: "Veramine-banner",
	description: "Example block Veramine",
	category: "wedding",
	icon: "dashicons dashicons-info",

	attributes: {
		title: {
			type: "string",
			default: "<br>",
		},
		header_txt: {
			type: "string",
			default: "!",
		},
		description: {
			type: "string",
			default:
				"!",
		},
		logo: {
			type: "object",
			default: {
				url:
					PV_Admin.PV_BASE_URL +
					"/assets/img/blocks/product/veramine/logo_veramin.svg",
				id: 1,
				alt: "",
			},
		},
		image: {
			type: "object",
			default: {
				url:
					PV_Admin.PV_BASE_URL +
					"/assets/img/blocks/product/veramine/icon_baner_veramin.svg",
				id: 1,
				alt: "",
			},
		},
		steps: {
			type: "array",
			default: [
				{
					text: " ",
					color: "#F3F4FD",
				},
				{
					text: " 1 ",
					color: "#EDFAFE",
				},
				{
					text: "CPU 1 % 20M3 AM",
					color: "#EBF5FF",
				},
			],
		},
		config: {
			type: "object",
			default: {},
		},
	},
	example: {},
	getEditWrapperProps() {
		return { "data-align": "full" };
	},
	edit: Edit,
});
